.wapper {
    display: flex;
}
.container {
    flex: 3;
    background-color: var(--3rd-background-color);
    height: 100vh;
    overflow-y: auto;
}
.content {
    max-width: var(--default-layout-width, 1200px);
    margin: auto;
}

// Custom scroll bar

.container::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    background-color: var(--3rd-background-color);
}
.container::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    background-color: var(--3rd-background-color);
}
.container::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: var(--hover-color);
}